
body {
    background-color: #ebebeb;
}
#app {
    padding-left: 25%;
    padding-right: 25%;
}

.header-logo {
    float: left;
}
.header-logo img {
    width: 100px;
}
.header-menu {
    float: right;
}
.header-menu li {
    list-style-type: none;
    display: inline;
}
.header-menu li::after {
    content: '|';
    padding-left: 10px;
    padding-right: 3px;
    color: #cacaca;
}
.header-menu li:last-child::after {
    content: '';
}
a:link, a:hover, a:visited {
    text-decoration: none;
    color: black;
}

.footer {
    background: white;
    margin: 0 auto;
    margin-top: 2%;
    padding-top: 3%;
    height: 150px;
    -webkit-box-shadow: 3px 3px 3px 3px #d0d0d0;
            box-shadow: 3px 3px 3px 3px #d0d0d0;
}
.writer {
    font-size: 25px;
    font-weight: bold;
    text-align: center;
}
.descript {
    font-size: 17px;
    text-align: center;
}

.body-row {
    clear: both;
    margin: 0 auto;
    padding-top: 3%;
    padding-left: 10%;
    padding-right: 10%;
    height: 900px;
    background-color: white;
    -webkit-box-shadow: 3px 3px 3px 3px #d0d0d0;
            box-shadow: 3px 3px 3px 3px #d0d0d0;
}
h1 {
    padding-bottom: 5%;
}
.body-btn-box {
    text-align: right;
}
.body-btn-box button {
    height: 35px;
    width: 80px;
    border: none;
    border-radius: 5px;
    background: #33c197;
    color: white;
}
.tb-header {
    border-bottom: solid 1px black;
}
.tb-row {
    border-bottom: solid 1px #d0d0d0;
}
.tb p {
    display: inline-block;
}
.tb p:nth-child(1) {
    width: 10%;
}
.tb p:nth-child(2) {
    width: 60%;
}
.tb p:nth-child(3) {
    width: 15%;
}

.paging-row {
    text-align: center;
}
.paging-row li {
    list-style-type: none;
    display: inline;
}
.paging-row button {
    height: 30px;
    width: 40px;
    border: none;
    background: #44cea5;
    color: white;
    font-weight: bold;
}
.paging-row span {
    font-size: 16px;
}
.paging-row span::after {
    content: '';
    padding-left: 5px;
    padding-right: 5px;
}

.join-row {
    clear: both;
    padding-top: 4%;
}
.join-form {
    margin: 0 auto;
    padding-top: 4%;
    padding-left: 10%;
    height: 400px;
    background-color: white;
    -webkit-box-shadow: 3px 3px 3px 3px #d0d0d0;
            box-shadow: 3px 3px 3px 3px #d0d0d0;
}
h1 {
    padding-bottom: 50px;
}
.join-form input {
    border-style: none;
    border-bottom: solid black 1px;
    height: 30px;
    width: 80%;
    outline: none;
    padding-left: 2%;
}
.join-form p:last-child {
    text-align: right;
    padding-right: 15%;
}
.join-form button {
    height: 35px;
    width: 80px;
    border: none;
    border-radius: 5px;
}
.join-form button:first-child {
    background: #33c197;
    color: white;
}

